import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  name: String;
  username: String;
  email: String;
  password: String;

  constructor(private _validateService : ValidateService, 
              private _flashMessagesService : FlashMessagesService,
              private _authService : AuthService,
              private _router : Router) { }

  ngOnInit() {
  }

  onRegisterSubmit(){
    // valores destes campos vêm do form
    var user = {
      name: this.name,
      email: this.email,
      username: this.username,
      password: this.password
    }

    // validar se campos preenchidos
    if (!this._validateService.validateRegister(user)) {
      //console.log('campos n preenchidos');
      this._flashMessagesService.show('Preencher todos os campos!', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }

    // validar email bem construido
    if (!this._validateService.validateEmail(user.email)) {
      //console.log('email errado');
      this._flashMessagesService.show('Usar email válido!', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }

    // registar user
    this._authService.registerUser(user).subscribe(data =>  {
      if(data.success) {
        this._flashMessagesService.show('Registado!', {cssClass: 'alert-success', timeout: 3000});
        this._router.navigate(['/login']);
      } else {
        this._flashMessagesService.show('Algo de errado!', {cssClass: 'alert-danger', timeout: 3000});
        this._router.navigate(['/register']);
      }
    });
  }

}



